<template>
	<div id="finance-current">
		<public-header title="账户明细"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group flex-lr">
					<div class="tab-item" :class="{active: filter.items.trade_type == 0}" @click="onToggleStatus(0)">全部</div>
					<div class="tab-item" :class="{active: filter.items.trade_type == 1}" @click="onToggleStatus(1)">订单入账</div>
					<div class="tab-item" :class="{active: filter.items.trade_type == 2}" @click="onToggleStatus(2)">申请提现</div>
					<div class="tab-item" :class="{active: filter.items.trade_type == 3}" @click="onToggleStatus(3)">其他变动</div>
					<div class="tab-item" :class="{active: filter.items.trade_type == 4}" @click="onToggleStatus(4)">买单订单</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="current-list"
			>
				<div class="current-item" v-for="(item, index) in records.data" :key="item.id">
					<router-link tag="div" :to="util.getUrl({path: '/pages/finance/currentDetail', query:{id: item.id}})" class="border-1px-b padding-10">
						<div class="current-detail font-14 padding-10-b">
							<span class="font-bold c-default" v-if="item.trade_type == 1">订单入账</span>
							<span class="font-bold c-default" v-else-if="item.trade_type == 2">申请提现</span>
							<span class="font-bold c-default" v-else-if="item.trade_type == 3">其他变动</span>
							<span class="font-bold c-default" v-else-if="item.trade_type == 4">买单订单入驻</span>
							<span class="font-bold c-primary" v-if="item.fee > 0">+{{item.fee}}</span>
							<span class="font-bold c-danger" v-else>{{item.fee}}</span>
						</div>
						<div class="current-detail font-14">
							<span class="c-gray">{{item.addtime_cn}}</span>
							<span class="c-gray">余额：￥{{item.amount}}</span>
						</div>
					</router-link>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading:true,
				isRefresh: false,
				filter: {
					items: {
						trade_type: '0'
					}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'manage/finance/current'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.trade_type == value) {
					return;
				}
				this.filter.items.trade_type = value;
			}
		},
		mounted(){
			this.onLoad();
		},

		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#finance-current .content .current-item{
		background-color: #fff;
	}
	#finance-current .content .current-item:first-child{
		border-top: 1px solid #e0e0e0;
	}
	#finance-current .content .current-item .current-detail {
		display: flex;
		flex-wrap: nowrap;
		justify-content: space-between;
		align-items: center;
	}
</style>


